import React from 'react';

import { Upload, message, Button, Popover } from 'antd';
import { UploadOutlined } from '@ant-design/icons';

import 'antd/dist/antd.css';

import Url from './../../../../../url/url.js'
import { Toast } from './../../../../../alert/alert.js'

import styled from '@emotion/styled'

const UploadPupils = () => {

	const Box = styled.div({
		'.ant-upload-list': {
			display: 'none'
		}
	})

	const props = {
	  name: 'file',
	  action: `${Url}/CRM/Pupils/uploadFile`,
	  accept: '.csv,.xlsx',
	  headers: {
	    authorization: 'authorization-text',
	  },
	  onChange(info) {
	  	
	    if (info.file.status === 'done') {

	    	Toast.fire({ 
	    		icon: 'success', 
	    		title: `Фаил ${info.file.name}, успешно загружен` 
	    	})
	    } else if (info.file.status === 'error') {
	    	Toast.fire({ 
	    		icon: 'error', 
	    		title: `В ${info.file.name}, ошибки на строках: ${info.file.response.message.join(", ")}` 
	    	})
	    }
	  }
	};

	return (
	<Box>
		<Upload {...props}>
			<Popover
				title={'Загрузчик'}
				content={'Загрузите файлы для добавления учеников'}
				placement="right"
				trigger={'hover'}>
				{/*<UploadOutlined style={{fontSize: '30px'}}/>*/}
		    	<Button style={{border: 'none'}} icon={<UploadOutlined style={{fontSize: '30px'}}/>}></Button>
		    </Popover>
		</Upload>
	</Box>
	)
}

export default UploadPupils